<template>
  <v-container class="my-5">
    <v-layout row wrap align-center justify-space-around class="lighten-2" flat>
      <span class="mr-3 "><v-icon>dashboard</v-icon> Toolbar</span>
      <v-flex md2>
        <v-btn color="success" small @click.stop="loadDialog"
          ><v-icon>add</v-icon> Add New</v-btn
        >
        <v-dialog
          max-width="700px"
          persistent
          v-model="dialog"
          transition="fab-transition"
          :retain-focus="false"
        >
          <v-card>
            <v-card-title>
              <h2>Add a New Product</h2>
            </v-card-title>
            <v-card-text>
              <v-form
                class="px-2"
                ref="form"
                @submit.prevent="doAddProduct"
                enctype="multipart/form-data"
              >
                <v-text-field
                  outlined
                  label="Name"
                  :rules="nameRules"
                  v-model="name"
                ></v-text-field>
                <v-textarea
                  outlined
                  label="Description"
                  :rules="descriptionRules"
                  v-model="description"
                ></v-textarea>
                <v-text-field
                  outlined
                  label="Quantity"
                  :rules="numberRules"
                  v-model="quantity"
                ></v-text-field>
                <v-select
                  outlined
                  :items="categoryItems"
                  :rules="[(v) => !!v || 'Category is required']"
                  v-model="categoryId"
                  label="Category"
                  :retain-focus="false"
                ></v-select>
                <v-switch v-model="status" label="Status"></v-switch>
                <!-- <input
				type="file"
				name="image"
				hidden
				ref="fileInput"
				accept="image/*"
				@change="onFilePicked"
				/> -->
                <!-- <v-flex md4 v-for="(image, index) in imageUrl" :key="index">
                  <img width="100px" :src="image" alt="" />
                </v-flex> -->

                <v-card
                  class="d-inline-block mx-auto mr-2 mb-3"
                  color="grey lighten-3"
                  v-for="(image, index) in imageUrl"
                  :key="index"
                >
                  <v-btn @click="deleteImage(image)" text small>
                    <v-icon small color="red">delete</v-icon>
                  </v-btn>

                  <v-container>
                    <v-row justify="space-between">
                      <v-col cols="auto">
                        <v-img height="200" width="200" :src="image"></v-img>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card>

                <v-file-input
                  outlined
                  label="Image"
                  accept="image/*"
                  color="deep-purple accent-4"
                  placeholder="Pick an image"
                  prepend-icon="mdi-camera"
                  counter
                  small
                  multiple
                  :rules="imageRules"
                  v-model="image"
                  :show-size="1000"
                  @change="onPickFile"
                >
                  <template v-slot:selection="{ index, text }">
                    <v-chip
                      v-if="index < 4"
                      color="deep-purple accent-4"
                      dark
                      label
                      small
                    >
                      {{ text }}
                    </v-chip>

                    <span
                      v-else-if="index === 4"
                      class="overline grey--text text--darken-3 mx-2"
                    >
                      +{{ image.length - 2 }} File(s)
                    </span>
                  </template>
                </v-file-input>
                <v-layout>
                  <v-flex md12 xs12 right>
                    <v-btn color="success" type="submit" :loading="loading"
                      >Add</v-btn
                    >
                    <v-btn class="ml-2" color="error" @click.prevent="close()"
                      >Done</v-btn
                    >
                  </v-flex>
                </v-layout>
              </v-form>
            </v-card-text>
          </v-card>
        </v-dialog>
      </v-flex>

      <v-flex md2>
        <v-btn color="info" small
          ><v-icon>vertical_align_top</v-icon> Export Exel</v-btn
        >
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions, mapState, mapGetters } from "vuex";
export default {
  data() {
    return {
      name: "",
      description: "",
      quantity: "",
      image: [],
      categoryId: "",
      status: false,
      dialog: false,

      categoryItems: [],
      imageUrl: [],
      loading: false,
      numberRules: [
        (v) =>
          (!isNaN(parseFloat(v)) && v >= 0 && v <= 999) ||
          "Number has to be between 0 and 999",
        (v) => !!v || "Quantity is required",
        (v) =>
          (v && v.length <= 10) || "Quantity must be less than 10 characters",
      ],
      nameRules: [
        (v) => !!v || "Name is required",
        (v) => (v && v.length <= 25) || "Name must be less than 10 characters",
      ],
      descriptionRules: [
        (v) => !!v || "Description is required",
        (v) =>
          (v && v.length <= 100) ||
          "Description must be less than 10 characters",
      ],
      imageRules: [(v) => (v && v.length > 0) || "File is required"],
    };
  },
  mounted() {
    this.getListCategory();
  },
  computed: {
    ...mapState("product", ["products"]),
    ...mapGetters("category", ["getCategory"]),
  },
  methods: {
    ...mapActions("product", ["addProduct"]),
    ...mapActions("category", ["getListCategory"]),
    async onPickFile() {
      let images = this.image;
      let arrImage = this.imageUrl;
      if (images.length > 0) {
        images.forEach((value) => {
          const fileReader = new FileReader();
          fileReader.readAsDataURL(value);
          fileReader.addEventListener("load", function() {
            arrImage.push(fileReader.result);
          });
        });
      } else {
        this.imageUrl = [];
      }
    },
    loadDialog() {
      this.dialog = true;
      this.status = false;
      this.categoryItems = this.getCategory;
    },
    close() {
      this.dialog = false;
      this.$refs.form.reset();
    },
    doAddProduct() {
      if (this.$refs.form.validate()) {
        const formData = new FormData();
        let images = this.image;
        if (images.length > 0) {
          for (let value of images) {
            formData.append("image", value);
          }
        }
        formData.append("name", this.name);
        formData.append("description", this.description);
        formData.append("quantity", this.quantity);
        formData.append("status", this.status);
        formData.append("categoryId", this.categoryId);

        this.addProduct(formData)
          .then((res) => {
            let notify = {
              color: "success",
              text: res,
            };
            this.$emit("Notify", notify);
            this.loading = true;
            this.$refs.form.reset();
            this.dialog = false;
          })
          .catch((err) => {
            let notify = {
              color: "error",
              text: err,
            };
            this.$emit("Notify", notify);
          });
      }
      this.loading = false;
    },
    deleteImage(image) {
      let remove = "";
      for (let i of this.imageUrl) {
        if (i === image) {
          remove = i;
        }
      }
      this.image.splice(remove, 1);
      this.imageUrl.splice(remove, 1);
    },
  },
};
</script>

<style></style>
