<template>
  <div>
    <h2 class="subheading">Product</h2>
    <v-snackbar
      v-model="snackbar"
      :timeout="action ? 100000 : 3000"
      top
      right
      :color="color"
    >
      <span>{{ text }}</span>
      <v-btn v-if="action" text @click="doDelete(action)">Yes</v-btn>
      <v-btn
        text
        color="white"
        @click="snackbar = false"
        v-if="action ? (textBtn = 'No') : (textBtn = 'Close')"
        >{{ textBtn }}</v-btn
      >
    </v-snackbar>
    <NavbarProduct @Notify="notifi" />
    <v-container class="my-5">
      <DatatableProduct :items="products" @Notify="notifi" />
    </v-container>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import NavbarProduct from "@/components/Product/NavbarProduct.vue";
import DatatableProduct from "@/components/Product/DatatableProduct.vue";
export default {
  data() {
    return {
      snackbar: false,
      color: "",
      text: "",
      action: "",
      textBtn: "",
    };
  },
  components: {
    NavbarProduct,
    DatatableProduct,
  },
  created() {
    this.getProducts();
  },
  computed: {
    ...mapState("product", ["products"]),
  },
  methods: {
    ...mapActions("product", ["getProducts", "deleteProduct"]),
    notifi(data) {
      this.snackbar = true;
      this.text = data.text;
      this.color = data.color;
      this.action = data.id;
    },
    doDelete(productId) {
      this.deleteProduct(productId);
      this.snackbar = false;
    },
  },
};
</script>

<style></style>
