<template>
  <v-container fluid>
    <nav>
      <v-navigation-drawer v-model="drawer" right app width="330">
        <v-list-item class=" mb-2 mt-2 ml-2">
          <v-list-item-title>
            <h3 class="font-weight-regular">UPDATE ITEM</h3>
          </v-list-item-title>
          <v-icon @click.stop="drawer = !drawer">clear</v-icon>
        </v-list-item>
        <v-divider></v-divider>
        <v-list-item>
          <v-card-text>
            <v-container
              class="fill-height"
              fluid
              justify-center
              v-if="image.length <= 0"
            >
              <v-card outlined v-for="(image, index) in imageUrl" :key="index">
                <v-container fluid>
                  <v-row justify="space-between">
                    <v-col cols="auto">
                      <v-img
                        v-if="imageUrl"
                        height="150"
                        width="400"
                        :src="
                          `https://congngheso1.herokuapp.com${image.slice(6)}`
                        "
                      ></v-img>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card>
            </v-container>
            <v-container class="fill-height" fluid justify-center v-else>
              <v-card outlined v-for="(image, index) in imageUrl" :key="index">
                <v-container fluid>
                  <v-row justify="space-between">
                    <v-col cols="auto">
                      <v-img height="150" width="400" :src="image"></v-img>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card>
            </v-container>
            <v-layout class="mb-1" v-show="imageUrl.length > 0">
              <v-row wrap>
                <v-flex xs6 md6>
                  <v-btn color="purple" text class="white--text"
                    >Upload Image</v-btn
                  >
                </v-flex>
                <v-flex xs6 md6>
                  <v-btn text class="red--text" @click="removeImage"
                    >Remove Image</v-btn
                  >
                </v-flex>
              </v-row>
            </v-layout>
            <v-form
              ref="form"
              @submit.prevent="doUpdateProduct"
              enctype="multipart/form-data"
            >
              <v-text-field
                outlined
                label="Name"
                :rules="nameRules"
                v-model="name"
              ></v-text-field>
              <v-textarea
                outlined
                label="Description"
                :rules="descriptionRules"
                v-model="description"
                rows="4"
              ></v-textarea>
              <v-text-field
                outlined
                label="Quantity"
                :rules="numberRules"
                v-model="quantity"
              ></v-text-field>
              <v-select
                outlined
                :items="categoryItems"
                :rules="[(v) => !!v || 'Category is required']"
                v-model="categoryId"
                label="Category"
                :retain-focus="false"
              ></v-select>
              <!-- <input
				type="file"
				name="image"
				hidden
				ref="fileInput"
				accept="image/*"
				@change="onFilePicked"
				/> -->
              <!-- <v-flex md4 v-for="(image, index) in imageUrl" :key="index">
                  <img width="100px" :src="image" alt="" />
                </v-flex> -->

              <v-file-input
                v-show="imageUrl.length === 0"
                outlined
                label="Image"
                accept="image/*"
                color="deep-purple accent-4"
                placeholder="Pick an image"
                prepend-icon="mdi-camera"
                counter
                small
                multiple
                :rules="imageRules"
                v-model="image"
                :show-size="1000"
                @change="onPickFile"
              >
                <template v-slot:selection="{ index, text }">
                  <v-chip
                    v-if="index < 4"
                    color="deep-purple accent-4"
                    dark
                    label
                    small
                  >
                    {{ text }}
                  </v-chip>

                  <span
                    v-else-if="index === 4"
                    class="overline grey--text text--darken-3 mx-2"
                  >
                    +{{ image.length - 2 }} File(s)
                  </span>
                </template>
              </v-file-input>
              <v-footer>
                <v-flex md12 xs12 right>
                  <v-btn color="info" type="submit" :loading="loading"
                    >SAVE</v-btn
                  >
                  <v-btn
                    class="ml-2"
                    outlined
                    color="error"
                    @click.prevent="drawer = !drawer"
                    >Cancel</v-btn
                  >
                </v-flex>
              </v-footer>
            </v-form>
          </v-card-text>
        </v-list-item>
      </v-navigation-drawer>
    </nav>
    <v-layout wrap row>
      <v-flex xs12 md10 offset-md1>
        <v-card>
          <v-card-title>
            Product List
            <v-spacer></v-spacer>
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Search"
              single-line
              hide-details
            ></v-text-field>
          </v-card-title>
          <v-data-table
            :headers="headers"
            :items="items"
            :search="search"
            class="elevation-1"
          >
            <template v-slot:item.imagePath="{ item }" slot-scope>
              <img
                v-show="item.imagePath"
                :src="
                  `https://congngheso1.herokuapp.com${item.imagePath[0].slice(
                    6
                  )}`
                "
                width="150px"
              />
            </template>
            <template v-slot:item.status="{ item }" slot-scope>
              <v-chip
                @click="doUpdateStatus(item)"
                v-if="item.status == true"
                color="info"
                small
                >Active</v-chip
              >
              <v-chip @click="doUpdateStatus(item)" v-else color="red" small
                >Unactive</v-chip
              >
            </template>
            <template v-slot:item.id="{ item }" slot-scope>
              <v-flex xs12 mb-2>
                <v-btn class="red mr-1" small @click="doDelete(item.id)">
                  <v-icon small>delete</v-icon>Delete
                </v-btn>
              </v-flex>

              <v-flex xs12>
                <v-btn class="primary mr-1" small @click.stop="update(item)">
                  <v-icon small>system_update_alt</v-icon>Update
                </v-btn>
              </v-flex>
            </template>
            <!-- <template #item="{item}" scope>
              <tr>
                <td>{{ item.name }}</td>
                <td>
                  <img
                    :src="`http://localhost:3000${item.imagePath[0]}`"
                    width="150px"
                  />
                </td>
                <td>{{ item.description }}</td>
                <td>{{ item.quantity }}</td>
                <td>
                  <v-chip v-if="item.status == true" color="info" small
                    >Active</v-chip
                  >
                  <v-chip v-else color="red" small>Unactive</v-chip>
                </td>
              </tr>
            </template> -->
          </v-data-table>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
// import axios from "axios";

import { mapActions, mapGetters, mapState } from "vuex";
export default {
  data() {
    return {
      drawer: false,
      search: "",
      headers: [
        {
          text: "NAME",
          align: "start",
          sortable: true,
          value: "name",
        },
        { text: "IMAGE", value: "imagePath", sortable: false },
        { text: "DESCRIPTION", value: "description", sortable: false },
        { text: "QUANTITY", value: "quantity" },
        { text: "STATUS", value: "status", sortable: false },
        { text: "ACTION", value: "id", sortable: false },
      ],
      id: "",
      name: "",
      description: "",
      quantity: "",
      image: [],
      categoryId: "",
      status: false,
      dialog: false,

      categoryItems: [],
      imageUrl: [],
      loading: false,

      numberRules: [
        (v) =>
          (!isNaN(parseFloat(v)) && v >= 0 && v <= 999) ||
          "Number has to be between 0 and 999",
        (v) => !!v || "Quantity is required",
      ],
      nameRules: [
        (v) => !!v || "Name is required",
        (v) => (v && v.length <= 25) || "Name must be less than 10 characters",
      ],
      descriptionRules: [
        (v) => !!v || "Description is required",
        (v) =>
          (v && v.length <= 100) ||
          "Description must be less than 10 characters",
      ],
      imageRules: [(v) => (v && v.length > 0) || "File is required"],
    };
  },
  props: ["items"],
  //   created() {
  //     this.getProducts().then((res) => {
  //       this.desserts = res;
  //     });
  //   },
  mounted() {
    this.getListCategory();
  },
  computed: {
    ...mapGetters("category", ["getCategory"]),
    ...mapState("product", ["products"]),
  },
  methods: {
    // async getData() {
    //   await axios.get("http://localhost:3000/api/product").then((res) => {
    //     console.log((this.desserts = res.data.product));
    //   });
    ...mapActions("category", ["getListCategory"]),
    ...mapActions("product", ["updateStatus", "updateProduct"]),
    doUpdateStatus(item) {
      this.updateStatus(item);
      let notify = {
        color: "success",
        text: `${item.name} Update Status Suceess`,
      };
      this.$emit("Notify", notify);
    },
    doDelete(productId) {
      let notify = {
        color: "error",
        text: "Are you sure ?",
        id: productId,
      };
      this.$emit("Notify", notify);
    },
    update(item) {
      this.id = item.id;
      this.drawer = true;
      this.image = [];
      this.categoryItems = this.getCategory;
      this.imageUrl = item.imagePath;
      this.name = item.name;
      this.description = item.description;
      this.quantity = item.quantity;
      this.categoryId = item.category;
    },
    async onPickFile() {
      let images = this.image;
      let arrImage = this.imageUrl;
      if (images.length > 0) {
        images.forEach((value) => {
          const fileReader = new FileReader();
          fileReader.readAsDataURL(value);
          fileReader.addEventListener("load", function() {
            arrImage.push(fileReader.result);
          });
        });
      } else {
        this.imageUrl = [];
      }
    },
    removeImage() {
      this.imageUrl = [];
      this.image = [];
    },
    doUpdateProduct() {
      const formData = new FormData();
      const productData = {
        id: this.id,
        name: this.name,
        description: this.description,
        quantity: this.quantity,
        category: this.categoryId,
      };
      let images = this.image;
      if (images.length > 0) {
        for (let value of images) {
          formData.append("image", value);
        }
      }
      for (let pro in productData) {
        formData.append(pro, productData[pro]);
      }
      // const nameProduct = formData.get("name");
      // const pro = this.products.find((i) => {
      //   return i.name.toLowerCase() === nameProduct.toLowerCase();
      // });
      // if (pro) {
      //   let notify = {
      //     color: "error",
      //     text: "Product exits !!!",
      //   };
      //   this.$emit("Notify", notify);
      // } else {
      let notify = {
        color: "",
        text: "",
      };
      this.updateProduct(formData)
        .then((res) => {
          notify.color = "success";
          notify.text = res;
          this.$emit("Notify", notify);
          this.drawer = false;
        })
        .catch((err) => {
          notify.color = "error";
          notify.text = err;
          this.$emit("Notify", notify);
        });

      // }
      // formData.forEach((value, key) => {
      //   console.log(key + " " + value);
      // });
    },
  },
};
</script>

<style></style>
